.subscription-block {
	position: relative;
	max-width: 690px;
	margin: 0 auto;

	@include min-w(lg) {
		max-width: 970px;
	}

	@include min-w(xxl) {
		max-width: 1218px;
	}

	&:before {
		content: '';
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, 0);
		display: block;
		width: 100vw;
		height: 54%;
		background-color: $white;

		@include min-w(xxl) {
			height: 41%;
		}
	}

	&__title {
		text-align: center;
		padding: 0 10px;

		@include min-w(lg) {
			padding: 0 30px 0 0;
		}

		@include min-w(xxl) {
			text-align: left;
			padding: 0;
		}
	}

	&__decoration-pentagons {
		position: absolute;
		left: -50px;
		bottom: -40px;
		width: 130px;
		z-index: 1;

		@include min-w(md) {
			bottom: -50px;
		}

		@include min-w(lg) {
			width: 165px;
			left: -85px;
			bottom: -70px;
		}

		@include min-w(xxl) {
			width: 200px;
			left: -60px;
		}
	}

	&__main-content {
		position: relative;
		z-index: 5;
		background-color: $gray-900;
		border-radius: 30px;
		color: $white;
		padding: 45px 25px 30px;

		@include min-w(md) {
			padding: 45px 50px 55px;
		}

		@include min-w(lg) {
			padding: 70px 60px 90px;
		}

		@include min-w(xxl) {
			padding: 100px 165px 135px;
		}
	}
}