.socials-list {
	@include listreset;

	display: inline-flex;
	flex-wrap: wrap;
	align-items: center;

	&:hover {
		.socials-list__link {
			background-color: rgba($white, 0.5);

			&:hover {
				color: $secondary;
				background-color: $white;
			}
		}
	}

	&__item {
		margin-top: 5px;
		margin-bottom: 5px;

		&:not(:last-child) {
			margin-right: 20px;

			@include min-w(lg) {
				margin-right: 24px;
			}
		}
	}

	&__link {
		@include sizeElement(1.875em);
		@include inlineFlexCenter;

		border-radius: 50%;
		background-color: $white;
		color: $primary;
		font-size: 13px;
		transition: color 0.3s, background-color 0.3s;
		overflow: hidden;

		@include min-w(lg) {
			font-size: 16px;
		}

		.fab.fa-instagram {
			font-weight: 700;
			font-size: 1.125em;
		}
	}
}