.contacts-list {
	@include listreset;

	display: flex;
	flex-wrap: wrap;
	font-size: 14px;
	line-height: 1.1;
	margin-left: -8px;

	@include min-w(md) {
		font-size: 18px;
	}

	@include min-w(lg) {
		font-size: 16px;
	}

	&__item {
		padding: 0 8px;
		margin: 5px 0;

		&:not(:first-child) {
			border-left: 1px solid $white;
		}
	}

	&__link {
		display: inline-block;
		border-bottom: 1px solid $primary;
		transition: color 0.3s, border-color 0.3s;

		&:hover {
			border-color: transparent;
		}
	}
}