.form-control {
	border-top: none;
	border-left: none;
	border-right: none;

	@include max-w(lg) {
		font-size: 16px;
	}
}

.form-group {
	@include max-w(lg) {
		margin-bottom: 45px;
	}

	@include max-w(md) {
		margin-bottom: 40px;
	}
}

textarea {
	&.form-control {
		height: 120px;
	}
}

form {
	button {
		&[type="submit"] {
			@include max-w(md) {
				display: block;
				width: 100%;
			}
		}
	}
}

.subscription-form {
	max-width: 580px;
	text-align: center;

	@include min-w(md) {
		display: flex;
		align-items: center;
	}

	@include min-w(lg) {
		max-width: 825px;
	}

	&__input {
		flex-grow: 1;
		width: 100%;
		border: none;
		background-color: $white;
		font-size: 16px;
		color: $secondary;
		border-radius: 12px;
		padding: 12px 20px;
		margin-bottom: 15px;
		line-height: 1.25;

		@include max-w(md) {
			line-height: 1.65;
		}

		@include min-w(md) {
			margin-bottom: 0;
			font-size: 18px;
			padding: 15px 20px;
		}

		@include min-w(lg) {
			font-size: 20px;
			padding: 18px 25px;
			border-radius: 20px;
			line-height: 1.2;
		}

		@include min-w(xxl) {
			padding: 20px 25px;
		}

		@include placeholder {
			color: $secondary
		};
	}

	&__button {
		min-width: 186px;

		@include min-w(md) {
			margin-left: 12px;
			min-width: 173px;
		}

		@include min-w(lg) {
			min-width: 216px;
		}
	}
}