.icon-and-content-card {
	margin-bottom: 50px;

	&__icon-block {
		overflow: hidden;
	}

	&__icon-round {
		@include sizeElement(60px);
		@include flexCenter;

		border-radius: 50%;
		background-color: $primary;
		color: $white;
		overflow: hidden;
		transform: translate(0, 100%);
		transition: transform 1.5s cubic-bezier(.19, 1, .22, 1);

		&.-active {
			transform: translate(0, 0);
		}
	}

	&__icon {
		font-size: 32px;
	}

	&__body {
		padding-top: 20px;
	}

	&__title {
		color: $gray-700;
		letter-spacing: -0.7px;
		margin-right: 15px;
	}
}

.filled-card {
	position: relative;
	display: flex;
	flex-direction: column;
	width: 100%;
	margin-bottom: 25px;

	@include min-w(md) {
		margin-bottom: 35px;
	}

	@include min-w(xl) {
		margin-bottom: 60px;
	}

	&__wrap {
		position: relative;
		z-index: 5;
		height: 100%;
    display: flex;
    flex-direction: column;
		color: $white;
		border-radius: 20px;
		padding: 35px 28px 40px;

		@include min-w(md) {
			padding: 40px 32px 25px;
		}

		@include min-w(xxl) {
			padding: 35px 25px 30px;
		}
	}

	&.-black {
		.filled-card__wrap {
			background-color: $gray-900;
		}
	}

	&.-primary {
		.filled-card__wrap {
			background-color: $primary;
		}

		a {
			color: $white;

			&:hover {
				color: $gray-900;
			}
		}
	}

	&__main-content {
		flex-grow: 1;
	}

	&__title {
		display: flex;
		align-items: center;
		color: $white;
		margin-bottom: 20px;
		padding: 15px 0;

		@include min-w(xl) {
			margin-bottom: 15px;
		}

		&.-with-icon {
			font-size: 16px;
			font-weight: 400;
			padding: 0;

			@include min-w(xl) {
				font-size: 26px;
			}

			@include min-w(xxl) {
				font-size: 30px;
			}

			strong {
				font-weight: 700;
			}
		}
	}

	&__title-icon {
		max-width: 40px;
		flex-shrink: 0;
		margin-right: 10px;

		@include min-w(xl) {
			max-width: 65px;
			margin-right: 15px;
		}

		@include min-w(xxl) {
			max-width: 75px;
		}
	}

	&__description {
		a {
			transition: color 0.3s;
		}
	}

	&__main-content {
		margin-bottom: 20px;

		@include min-w(md) {
			margin-bottom: 10px;
		}

		@include min-w(xl) {
			margin-bottom: 50px;
		}

		@include min-w(xxl) {
			margin-bottom: 80px;
		}
	}

	&__link-block {
		text-align: right;
		margin-right: -10px;
	}

	&__decoration-pentagons {
		position: absolute;
		bottom: -45px;
		right: -65px;
		z-index: 1;
		width: 165px;

		@include min-w(xl) {
			bottom: -90px;
			right: -85px;
		}

		@include min-w(xxl) {
			width: 200px;
			bottom: -85px;
			right: -105px;
		}
	}
}

.team-member-card {
	display: flex;
	flex-direction: column;
	width: 100%;
	margin-bottom: 65px;

	@include min-w(md) {
		margin-bottom: 30px;
	}

	&__main-content {
		flex-grow: 1;
	}

	&__photo-block {
		max-width: 90px;
		margin-bottom: 10px;

		@include min-w(md) {
			max-width: 180px;
			margin-bottom: 35px;
		}

		@include min-w(lg) {
			max-width: 120px;
			margin-bottom: 20px;
		}

		@include min-w(xxl) {
			max-width: 142px;
			margin-bottom: 25px;
		}
	}

	&__photo {
		position: relative;
		padding-bottom: 100%;
		border-radius: 10px;
		overflow: hidden;

		@include min-w(lg) {
			border-radius: 20px;
		}
	}

	&__position {
		font-weight: 400;
		font-size: 13px;
		color: $gray-450 !important;
		margin-bottom: 0;

		@include min-w(md) {
			font-size: 15px;
		}

		@include min-w(lg) {
			font-size: 13px;
		}
	}

	&__name-and-position {
		margin-bottom: 20px;

		@include min-w(md) {
			margin-bottom: 30px;
		}

		@include min-w(lg) {
			margin-bottom: 15px;
		}
	}

	&__area-of-work-list {
		@include listreset;

		font-size: 14px;
		line-height: 1.6;
		margin-bottom: 30px;

		@include min-w(md) {
			font-size: 18px;
		}

		@include min-w(lg) {
			line-height: 1.4375;
			font-size: 15px;
			margin-bottom: 25px;
		}

		@include min-w(xxl) {
			font-size: 16px;
		}
	}

	&__description {
		font-size: 14px;
		line-height: 1.6;
		margin-bottom: 30px;

		@include min-w(md) {
			font-size: 18px;
		}

		@include min-w(lg) {
			font-size: 15px;
			line-height: 1.4375;
			margin-bottom: 20px;
		}

		@include min-w(xxl) {
			font-size: 16px;
		}

		p {
			margin-bottom: 0;
		}
	}
}

.careers-card {
	display: flex;
	flex-direction: column;
	background-color: $white;
	border-radius: 30px;
	box-shadow: 0px 15px 43px -10px $gray-160;
	padding: 50px 20px 60px;
	text-align: center;
	margin-bottom: 25px;
	width: 100%;

	@include min-w(md) {
		padding: 50px 20px;
		margin-bottom: 40px;
	}

	@include min-w(xl) {
		padding: 70px 30px;
	}

	&__main-content {
		flex-grow: 1
	}

	&__work-time-list {
		@include listreset;

		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: center;
		font-size: 16px;
		margin-bottom: 15px;

		@include min-w(md) {
			font-size: 20px;
		}

		li {
			&:not(:first-child) {
				&:before {
					content: '\00a0|\00a0';
					display: inline;
				}
			}
		}
	}

	&__btn {
		min-width: 215px;
	}
}