@include title-sizes();

body {
	@include max-w(xxl) {
		font-size: 17px;
		line-height: 1.6;
	}

	@include max-w(xl) {
		font-size: 16px;
		line-height: 1.5;
	}

	@include max-w(md) {
		font-size: 15px;
		line-height: 1.5;
	}
}

.text-block {
	p:last-child {
		margin-bottom: 0;
	}
}

p {
	@include max-w(xxl) {
		margin-bottom: 1.5rem;
	}
}

h1, .h1 {
	margin-bottom: 35px;
	line-height: 1.25;

	@include min-w(md) {
		line-height: 1.21428;
	}

	@include min-w(xxl) {
		line-height: 1.1;
	}

	&.display-1 {
		margin-bottom: 5px;
		letter-spacing: 0.03em;

		@include min-w(md) {
			margin-bottom: 15px;
		}

		@include min-w(lg) {
			margin-bottom: 10px;
		}

		@include min-w(xxl) {
			margin-bottom: 30px;
		}

		@include max-w(xxl) {
			font-size: 180px;
		}

		@include max-w(lg) {
			font-size: 130px;
		}

		@include max-w(md) {
			font-size: 70px;
		}
	}
}

h2, .h2 {
	margin-bottom: 30px;

	@include max-w(md) {
		line-height: 1.1428;
	}

	@include min-w(md) {
		margin-bottom: 40px;
	}

	@include min-w(xl) {
		margin-bottom: 85px;
	}
}

h3, .h3 {
	line-height: 1.4;
	margin-bottom: 20px;

	@include min-w(md) {
		margin-bottom: 35px;
	}

	@include min-w(lg) {
		margin-bottom: 25px;
	}
}

h4, .h4 {
	margin-bottom: 15px;

	@include max-w(lg) {
		line-height: 1.1;
	}

	@include min-w(xl) {
		margin-bottom: 25px;
	}

	@include min-w(xxl) {
		margin-bottom: 20px;
	}
}

h5, .h5 {
	margin-bottom: 15px;
}

h6, .h6 {
	margin-bottom: 5px;

	@include min-w(md) {
		margin-bottom: 10px;
	}

	@include min-w(lg) {
		margin-bottom: 5px;
	}

	@include min-w(xxl) {
		margin-bottom: 10px;
	}
}

.title-with-bracket {
	display: flex;
	align-items: center;

	@include min-w(md) {
		margin-bottom: 0;
	}

	&:after {
		content: "\e901";
		display: none;
		font-family: icomoon;
		font-size: 182px;
    line-height: 1;
		font-weight: 400;

		@include min-w(md) {
			display: inline-block;
		}

		@include min-w(lg) {
			font-size: 236px;
		}

		@include min-w(xxl) {
			font-size: 293px;
		}
	}

	&__text {
		display: flex;
		flex-direction: column;

		@include min-w(md) {
			padding: 15px 0;
		}
	}

	&.-light-bracket {
		&:after {
			color: $gray-150;
		}
	}

	&.-dark-bracket {
		&:after {
			color: $gray-950;
		}
	}

	&.-from-right {
		.title-with-bracket__text {
			@include min-w(md) {
				padding-right: 45px;
			}

			@include min-w(xxl) {
				padding-right: 30px;
			}
		}
	}

	&.-from-left {
		@include min-w(md) {
			flex-direction: row-reverse;
		}

		.title-with-bracket__text {
			@include min-w(md) {
				padding-left: 45px;
				flex-grow: 1;
			}

			@include min-w(xxl) {
				padding-left: 30px;
			}
		}

		&:after {
			transform: rotate(180deg);
		}
	}

	&.-remove-bracket-lg {
		@include max-w(lg) {
			display: block;
			margin-bottom: 35px;
		}

		&:after {
			@include max-w(lg) {
				content: none;
			}
		}

		.title-with-bracket__text {
			@include max-w(lg) {
				display: inline-flex;
				flex-direction: row;
				padding: 0;
			}

			> span {
				@include max-w(lg) {
					display: inline-block;
				}

				&:not(:first-child) {
					padding-left: 5px;
				}
			}
		}
	}
}