.section {
	position: relative;
	padding: 80px 0;

	&-welcome {
		@include bgCover;

		position: relative;
		padding-top: 100px;
		color: $white;

		@include min-w(md) {
			padding-top: 120px;
		}

		@include min-w(lg) {
			padding-top: 130px;
		}

		@include min-w(xxl) {
			padding-top: 155px;
		}

		&__title-holder {
			text-align: center;
			padding-bottom: 45px;

			@include min-w(md) {
				padding-bottom: 60px;
			}

			@include min-w(lg) {
				padding-bottom: 105px;
			}

			@include min-w(xxl) {
				padding-bottom: 115px;
			}
		}

		&__description {
			font-size: 18px;
			font-weight: 500;
			letter-spacing: 0.03em;

			@include min-w(md) {
				font-size: 20px;
			}

			@include min-w(lg) {
				font-size: 27px;
			}

			@include min-w(xxl) {
				font-size: 40px;
			}
		}
	}

	&-our-core-compencies {
		padding: 120px 0 80px;
		background-image: url('../img/decoration-line-1.svg'), url('../img/decoration-line-2.svg');
		background-position: top -400px right -250px, bottom 0 left 0;
		background-repeat: no-repeat;
		background-size: 500px, 700px;
		overflow: hidden;

		@include min-w(md) {
			padding: 135px 0 75px;
			background-position: top -800px right -280px, bottom -400px left 0px;
			background-size: 700px, 700px;
		}

		@include min-w(xl) {
			padding: 195px 0 165px;
			background-position: top -1100px right -200px, bottom -400px left -175px;
			background-size: auto;
		}

		@include min-w(xxl) {
			padding: 195px 0;
			background-position: top -1000px right, bottom -300px left;
		}

		&__img {
			margin-bottom: 60px;

			@include min-w(md) {
				min-width: 760px;
			}
		}

		&__block-with-image {
			@include min-w(md) {
				margin-bottom: 75px;
			}

			@include min-w(xl) {
				margin-bottom: 135px;
			}
		}

		&__block-with-middle-cards {
			margin-bottom: 50px;

			@include min-w(md) {
				margin-bottom: 0;
			}

			@include min-w(xl) {
				margin-bottom: 135px;
			}

			@include min-w(xxl) {
				margin-bottom: 230px;
			}
		}
	}

	&-about {
		background-color: $gray-100;
		padding: 40px 0 65px;

		@include min-w(md) {
			padding: 120px 0 70px;
		}

		@include min-w(xl) {
			padding: 145px 0 130px;
		}

		@include min-w(xxl) {
			padding: 155px 0 55px;
		}

		&__main-content {
			@include min-w(md) {
				display: flex;
				flex-wrap: wrap;
				align-items: center;
			}
		}

		&__title-block {
			@include min-w(md) {
				flex: 0 0 auto;
				width: auto;
				max-width: 100%;
			}
		}

		&__content-block {
			@include max-w(md) {
				font-size: 16px;
				line-height: 1.6;
			}

			@include min-w(md) {
				flex-basis: 0px;
				flex-grow: 1;
				max-width: 100%;
				padding-left: 30px;
			}

			@include min-w(lg) {
				padding-left: 65px;
				flex: 0 0 53%;
				max-width: 53%;
			}

			@include min-w(xxl) {
				flex: 0 0 52%;
				max-width: 52%;
			}
		}
	}

	&-the-team {
		position: relative;
		padding: 30px 0;
		overflow: hidden;

		@include min-w(md) {
			padding: 50px 0;
		}

		@include min-w(lg) {
			padding: 100px 0;
		}

		&:before {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: 45%;
			background-color: $gray-100;
			pointer-events: none;
		}

		&__block {
			position: relative;
			z-index: 5;

			&:after {
				content: '';
				display: block;
				background-color: $gray-900;
				position: absolute;
				top: 0;
				left: 50%;
				bottom: 0;
				width: 100vw;
				transform: translate(-50%, 0);
				pointer-events: none;

				@include min-w(sm) {
					background-image: url('../img/decoration-pentagons-dark.png');
					background-repeat: no-repeat;
					background-size: 220px;
					background-position: top right;
				}

				@include min-w(lg) {
					content: none;
				}
			}
		}

		&__decoration-pentagons {
			position: absolute;
			width: 165px;
			top: -45px;
			right: -80px;
			z-index: 1;
			display: none;

			@include min-w(lg) {
				display: block;
			}

			@include min-w(xxl) {
				width: 270px;
				top: -85px;
				right: -100px;
			}
		}

		&__block-wrap {
			position: relative;
			z-index: 5;
			display: flex;
			flex-direction: column-reverse;
			color: $white;
			border-radius: 30px;
			padding: 40px 0 65px;

			@include min-w(md) {
				padding: 90px 0 65px;
			}

			@include min-w(lg) {
				display: flex;
				flex-direction: row;
				flex-wrap: wrap;
				align-items: center;
				padding: 30px;
				background-color: $gray-900;
			}

			@include min-w(xl) {
				padding: 55px 35px 35px 95px;
			}

			@include min-w(xxl) {
				padding: 65px 35px 60px 110px;
			}
		}

		&__team-member-list {
			@include min-w(lg) {
				flex-basis: 0px;
				flex-grow: 1;
				max-width: 100%;
			}

			[class^="col"] {
				&:last-child {
					.team-member-card {
						@include max-w(md) {
							margin-bottom: 0;
						}
					}
				}
			}
		}

		&__title-block {
			@include min-w(lg) {
				flex: 0 0 395px;
				max-width: 395px;
				padding-left: 30px;
			}

			@include min-w(xxl) {
				flex: 0 0 450px;
				max-width: 450px;
			}
		}
	}

	&-careers {
		position: relative;
		padding: 25px 0 70px;

		@include min-w(md) {
			padding: 50px 0 80px;
		}

		@include min-w(lg) {
			padding: 80px 0 100px;
		}

		@include min-w(xl) {
			padding: 100px 0;
		}

		@include min-w(xxl) {
			padding: 170px 0 120px;
		}

		&__bg {
			@include absoluteCover;

			background-repeat: no-repeat;
			background-position: center;
			background-size: contain;
			z-index: 1;
			pointer-events: none;
		}

		&__main-content {
			position: relative;
			z-index: 5;
		}

		&__title-holder {
			margin-bottom: 40px;

			@include min-w(md) {
				margin-bottom: 55px;
				text-align: center;
			}
		}

		&__title {
			margin-bottom: 30px;

			@include max-w(md) {
				font-size: 37px;
				margin-bottom: 20px;
			}

			@include min-w(xl) {
				font-size: 64px;
			}
		}

		&__description {
			max-width: 730px;
			margin: 0 auto;
			font-size: 17px;
			line-height: 1.4;

			@include min-w(md) {
				font-size: 20px;
			}
		}

		&__link-block {
			text-align: right;
			margin-right: -10px;
		}
	}
}