.header {
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	z-index: $zindex-fixed;
	background-color: transparent;
	transition: background-color 0.3s;

	&:after {
		@include absoluteCover;

		content: '';
		display: block;
		background-color: $primary;
		transform: translate(0, -100%);
		transition: transform 0.3s;
	}

	&.-active {
		&:after {
			transform: translate(0, 0);
		}
	}

	&.-menu-open {
		@include max-w(md) {
			background-color: $primary;
		}

		.header__main-content {
			@include max-w(md) {
				border-color: rgba($white, 0.2);
			}
		}

		.header__nav-block {
			@include max-w(md) {
				opacity: 1;
				visibility: visible;
			}
		}
	}

	&__container {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		justify-content: space-between;

		@include max-w(md) {
			padding-left: 30px;
			padding-right: 30px;
		}

		@include min-w(xxl) {
			max-width: 1465px;
		}
	}

	&__main-content {
		position: relative;
		z-index: 5;
		transition: padding 0.3s, border-color 0.3s;
		padding: 25px 0;
		border-bottom: 2px solid transparent;

		@include min-w(md) {
			padding: 30px 0;
		}

		@include min-w(lg) {
			padding: 20px 0;
		}
	}

	&__logo {
		flex-shrink: 0;
		margin-right: 30px;
	}

	&__nav-block {
		@include max-w(md) {
			position: absolute;
			top: 86px;
			bottom: 0;
			left: 0;
			right: 0;
			width: 100%;
			height: calc(100vh - 86px);
			height: calc(var(--vh, 1vh) * 100 - 86px);
			background-color: $primary;
			opacity: 0;
			visibility: hidden;
			z-index: 5;
			transition: opacity 0.3s, visibility 0.3s;
		}
	}

	&__nav-block-wrap {
		@include max-w(md) {
			height: 100%;
		}
	}

	&__nav-block-inner {
		@include min-w(md) {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
		}

		@include max-w(md) {
			height: 100%;
			overflow: auto;
			max-width: map-get($container-max-widths, "sm");
			margin: 0 auto;
			width: 100%;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			padding: 22vh 30px 30px;
			padding: calc(var(--vh, 1vh) * 22) 30px 30px;
		}
	}

	&__navigation {
		+ .header__buttons-list {
			padding-top: 80px;

			@include min-w(md) {
				padding-top: 0;
				margin-left: 35px;
			}

			@include min-w(lg) {
				margin-left: 50px;
			}
		}
	}

	&__hamburger-menu {
		display: inline-flex;

		@include min-w(md) {
			display: none !important;
		}
	}
}

.header-logo {
	position: relative;
	display: inline-block;
	max-width: 16px;
	overflow: hidden;

	@include min-w(md) {
		max-width: 20px;
	}

	@include min-w(lg) {
		max-width: 25px;
	}

	@include min-w(xxl) {
		max-width: 30px;
	}

	&__img {
		transition: transform 0.5s;

		&:last-child {
			position: absolute;
			top: 100%;
			left: 50%;
			transform: translate(-50%, 20%)
		}
	}

	&:hover {
		.header-logo__img {
			&:first-child {
				transform: translate(0, -120%)
			}

			&:last-child {
				transform: translate(-50%, -100%)
			}
		}
	}
}

.header-navigation {
	@include listreset;

	font-size: 23px;
	line-height: 1.3;
	font-weight: 700;
	letter-spacing: 0.04em;

	@include min-w(md) {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		font-size: 16px;
	}

	@include min-w(lg) {
		font-size: 18px;
	}

	&__item {
		&:not(:last-child) {
			@include max-w(md) {
				margin-bottom: 50px;
			}
		}

		@include min-w(md) {
			margin: 5px 0;
		}

		+ .header-navigation__item {
			@include min-w(md) {
				margin-left: 40px;
			}

			@include min-w(lg) {
				margin-left: 70px;
			}
		}
	}

	&__link {
		position: relative;
		display: inline-block;
		color: $white;
		transition: color 0.3s;

		&:after {
			content: "";
			position: absolute;
			bottom: -3px;
			left: 0;
			width: 100%;
			height: 2px;
			background: linear-gradient(254.54deg, $white 0%, $primary 100%);
			transform: scaleX(0);
			transform-origin: right center;
			transition: transform .5s cubic-bezier(.694,0,.335,1);
		}

		&:hover {
			color: $white;

			&:after {
				transform: scaleX(1);
				transform-origin: left center;
			}
		}
	}
}

.header-buttons-list {
	@include listreset;

	&__item {
		+ .header-buttons-list__item {
			margin-top: 10px;

			@include min-w(md) {
				margin-left: 20px;
				margin-top: 0;
			}
		}
	}

	&__btn {
		@include max-w(md) {
			padding-top: 11px !important;
			padding-bottom: 11px !important;
			display: block;
			width: 100%;
			padding-top: 10px;
			padding-bottom: 10px;
		}
	}
}