.hamburger-menu {
	@include sizeElement(34px);

	display: inline-flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background-color: $gray-900;
	border-radius: 8px;

	&.-active {
		background-color: transparent;

		.hamburger-menu__bars {
			&:first-child,
			&:last-child {
				height: 3px;
				width: 31px;
				border-radius: 2px;
				transform-origin: center;
			}

			&:first-child {
				margin-bottom: -1.5px;
				transform: rotate(45deg);
			}

			&:last-child {
				margin-top: -1.5px;
				transform: rotate(-45deg);
			}

			&:nth-child(2) {
				display: none;
			}
		}
	}

	&__bars {
		width: 16px;
		height: 2px;
		background-color: $white;

		&:nth-child(2) {
			margin-top: 5px;
			margin-bottom: 5px;
		}
	}
}