.footer {
	padding: 55px 0 45px;
	background-color: #EB3878;
	color: $white;
	background-position: left bottom -20px;
	background-repeat: no-repeat;
	background-size: 210px;

	@include min-w(md) {
		background-position: left bottom;
		background-size: 280px;
	}

	@include min-w(lg) {
		padding: 90px 0 75px;
		background-size: 370px;
		background-position: left -70px bottom -40px;
	}

	@include min-w(xxl) {
		padding: 90px 0;
		background-position: left bottom;
	}

	&__title-holder {
		margin-bottom: 60px;

		@include min-w(md) {
			padding-bottom: 75px;
			padding-right: $grid-gutter-width !important;
			margin-bottom: 0;
		}

		@include min-w(lg) {
			padding-bottom: 100px;
		}
	}

	&__title {
		font-size: 28px;

		@include min-w(lg) {
			font-size: 34px;
			margin-bottom: 5px;
		}

		@include min-w(xl) {
			font-size: 40px;
		}
	}

	&__description {
		font-size: 18px;
		opacity: 0.5;
	}

	&__socials-list {
		margin-bottom: 10px;

		@include min-w(lg) {
			margin-bottom: 30px;
		}
	}

	&__copyright {
		font-size: 13px;
		opacity: 0.8;
		font-weight: 700;
		margin-bottom: 0;

		@include min-w(lg) {
			font-size: 16px;
		}
	}

	&__socials-and-copyright {
		padding-top: 105px;

		@include min-w(md) {
			padding-top: 0;
			margin-top: -75px;
		}

		@include min-w(lg) {
			margin-top: -100px;
		}
	}

	&__form-block {
		@include min-w(md) {
			max-width: 370px;
			margin-left: auto;
			padding-top: 5px;
			padding-bottom: 15px;
		}

		@include min-w(lg) {
			max-width: 595px;
		}

		@include min-w(xxl) {
			max-width: 735px;
		}
	}
}