@import "variables";

@font-face {
  font-family: '#{$icomoon-font-family}';
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?m1izpj');
  src:  url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?m1izpj#iefix') format('embedded-opentype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?m1izpj') format('truetype'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?m1izpj') format('woff'),
    url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?m1izpj##{$icomoon-font-family}') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

i {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: '#{$icomoon-font-family}' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.icomoon-bracket {
  &:before {
    content: $icomoon-bracket; 
  }
}
.icomoon-arrow-right {
  &:before {
    content: $icomoon-arrow-right; 
  }
}

