// vertical text top - bottom
.-vertical-text-top-bottom {
	writing-mode: vertical-rl;
	-webkit-writing-mode: vertical-rl;
	display: inline-block;
	margin: 0;
}

// vertical text bottom - top
.-vertical-text-bottom-top {
	writing-mode: vertical-rl;
	-webkit-writing-mode: vertical-rl;
	transform: rotate(180deg);
	display: inline-block;
	margin: 0;
}

.-visually-hidden {
	position: absolute;
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	clip: rect(0 0 0 0);
	overflow: hidden;
}

.-overflow-hidden {
	overflow: hidden;
}

.-text-overflow {
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: nowrap;
}

.-slick-with-flex {
	.slick-track {
		display: flex;
	}

	.slick-slide {
		display: flex;
		height: auto;

		> div {
			width: 100%;
		}
	}
}

.-slick-slides-spacing-15 {
	.slick-list {
		margin: 0 -15px;
	}

	.slick-slide {
		margin: 0 15px;
	}
}

.ofi {
	@include absoluteCover;

	object-fit: cover;
	font-family: 'object-fit: cover;';
}

.row {
	&.-gutter-width-74 {
		@include min-w(xl) {
			margin-left: -30px;
			margin-right: -30px;
		}

		@include min-w(xxl) {
			margin-left: -37px;
			margin-right: -37px;
		}

		[class^="col"] {
			@include min-w(xl) {
				padding-left: 30px;
				padding-right: 30px;
			}

			@include min-w(xxl) {
				padding-left: 37px;
				padding-right: 37px;
			}
		}
	}

	&.-gutter-width-64 {
		@include min-w(xl) {
			margin-left: -32px;
			margin-right: -32px;
		}


		[class^="col"] {
			@include min-w(xl) {
				padding-left: 32px;
				padding-right: 32px;
			}
		}
	}

	&.-gutter-width-42 {
		@include min-w(xl) {
			margin-left: -16px;
			margin-right: -16px;
		}

		@include min-w(xxl) {
			margin-left: -21px;
			margin-right: -21px;
		}

		[class^="col"] {
			@include min-w(xl) {
				padding-left: 16px;
				padding-right: 16px;
			}

			@include min-w(xxl) {
				padding-left: 21px;
				padding-right: 21px;
			}
		}
	}
}

.-white-titles {
	h1, .h1,
	h2, .h2,
	h3, .h3,
	h4, .h4,
	h5, .h5,
	h6, .h6 {
		color: $white;
	}
}

.fab {
	font-family: 'Font Awesome 5 Brands' !important;
}

.fa,
.fas,
.far {
	font-family: 'Font Awesome 5 Free' !important;
}

.text-gray-700 {
	color: $gray-700;
}