.btn {
	&:not(.btn-sm):not(.btn-lg) {
		@include max-w(xxl) {
			padding-top: 14px;
			padding-bottom: 14px;
		}

		@include max-w(lg) {
			padding-top: 10px;
			padding-bottom: 10px;
			border-radius: 12px;
		}

		@include max-w(md) {
			padding-top: 9px;
			padding-bottom: 9px;
		}
	}

	&-sm {
		@include min-w(lg) {
			min-width: 235px;
		}

		@include max-w(lg) {
			padding-top: 6px;
			padding-bottom: 6px;
			font-size: 16px;
			line-height: 1.5;
			border-radius: 14px;
		}
	}

	&-dark {
		&:hover {
			background-color: lighten($gray-800, 10%);
			border-color: lighten($gray-800, 10%);
		}
	}

	&-blue-dark-faded {
		background-color: $blue-dark-faded;
		border-color: $blue-dark-faded;
		color: $white;

		&:hover {
			color: $white;
			background-color: $black;
			border-color: $black;
		}

		&:not(:disabled):not(.disabled):active:focus,
		&:not(:disabled):not(.disabled).active:focus {
			box-shadow: 0 0 0 $input-btn-focus-width lighten($blue-dark-faded, 15%);
		}
	}

	&-light-pink {
		color: $primary;
		background-color: $pink-light;

		&:hover {
			background-color: darken($pink-light, 5%);
			border-color: darken($pink-light, 5%);
			color: $primary;
		}

		&:not(:disabled):not(.disabled):active:focus,
		&:not(:disabled):not(.disabled).active:focus {
			box-shadow: 0 0 0 $input-btn-focus-width lighten($primary, 25%);
		}
	}
}

.link-with-arrow {
	position: relative;
	font-size: 16px;
	font-weight: 500;
	padding-right: 45px;
	line-height: 1;
	transition: color 0.3s;

	@include min-w(xl) {
		padding-right: 65px;
		font-size: 17px;
	}

	@include min-w(xxl) {
		padding-right: 60px;
		font-size: 22px;
	}

	&:after {
		content: "\e900";
		display: inline-block;
		font-family: icomoon;
		position: absolute;
		top: 54%;
		right: 10px;
		transform: translate(0, -50%);
		font-size: 11px;
		transition: right 0.5s;

		@include min-w(xl) {
			font-size: 14px;
		}

		@include min-w(xxl) {
			font-size: 16px;
		}
	}

	&:hover {
		&:after {
			right: 0;
		}
	}

	&.-sm {
		font-size: 16px;
		padding-right: 41px;

		@include min-w(md) {
			font-size: 20px;
		}

		&:after {
			font-size: 8px;

			@include min-w(md) {
				font-size: 11px;
			}
		}
	}
}